import http from '../../tool/http'
import Vue from 'vue'
import { copy } from '../../tool/util'
import { Toast } from 'mint-ui'

export const SELECTION_RED_ENVELOPES_SET = 'SELECTION_RED_ENVELOPES_SET'
export const SELECTION_RED_ENVELOPES = 'SELECTION_RED_ENVELOPES'

export const GET_365_HOME_DATA = 'GET_365_HOME_DATA'

export const SET_365_PATTERN_DATA = 'SET_365_PATTERN_DATA'
export const SET_365_GENRE_DATA = 'SET_365_GENRE_DATA'
export const SET_365_PATTERN_INDEX = 'SET_365_PATTERN_INDEX'
export const SET_365_GENRE_INDEX = 'SET_365_GENRE_INDEX'
export const SET_365_FRUIT_RACING_INDEX = 'SET_365_FRUIT_RACING_INDEX'

export const SET_365_COMPETITION_LIST_DATA = 'SET_365_COMPETITION_LIST_DATA'

export const ADD_DEL_365_THROUGH_DATA = 'ADD_DEL_365_THROUGH_DATA'
export const MODIFY_365_DATA = 'MODIFY_365_DATA'
export const ADD_EARLY_BET_SUM = 'ADD_EARLY_BET_SUM'

export const HomeMutations = {
  [SET_365_PATTERN_DATA] (state, params) {
    state.Pattern = params
  },
  [SET_365_GENRE_DATA] (state, params) {
    state.Genre = params
  },
  [SET_365_PATTERN_INDEX] (state, params) {
    state.PatternIndex = params
  },
  [SET_365_GENRE_INDEX] (state, params) {
    state.GenreIndex = params
  },
  [SET_365_FRUIT_RACING_INDEX] (state, params) {
    state.MatchStatus = params
  },
  [SELECTION_RED_ENVELOPES_SET] (state, { target, redId, Coupon } /* query */) {
    // if (target === 'pass') {
    // 
    if (redId === 0) {
      // 
      Vue.set(state.PassStatus.UseRed, target, redId)
    } else if (state.PassStatus.UseRed[target] && state.PassStatus.UseRed[target].id === redId) {
      // 
      Vue.delete(state.PassStatus.UseRed, target)
    } else {
      // 
      const red = Coupon.find(i => i.id === redId)
      Vue.set(state.PassStatus.UseRed, target, { ...red })
    }
  },
  [SET_365_COMPETITION_LIST_DATA] (state, { index1, index2, index3, list }) {
    const Competition = copy(state.MatchSchedule[index1]) || []
    if (!Competition[index2]) {
      Competition[index2] = []
    }
    Competition[index2][index3] = list
    Vue.set(state.MatchSchedule, index1, Competition)
  },
  [ADD_DEL_365_THROUGH_DATA] (state, { initial, item, patternId }) {
    if (patternId === undefined) {
      throw new Error('patternId')
    }
    let PassStatus = {
      show: false,
      odds: 1,
      Betting: {},
      UseRed: {},
      betLength: 0
    }
    if (initial) {
      // 
      state.PassStatus = PassStatus
    } else if (item) {
      if (state.PassStatus.Betting[`${item.smid}`]) {
        // 
        if (state.PassStatus.Betting[`${item.smid}`][`${item.smpid}${item.bet_now}`]) {
          //   
          del()
        } else {
          //   
          cbk(replace, add)
        }
      } else {
        // 
        add()
      }
    }

    function replace () {
      // 
      PassStatus = copy(state.PassStatus)
      PassStatus.Betting[`${item.smid}`] = { [`${item.smpid}${item.bet_now}`]: item } // 
      PassStatus.show = true
      for (let i in state.PassStatus.Betting[`${item.smid}`]) {
        // odds
        const del = state.PassStatus.Betting[`${item.smid}`][i].odds[state.PassStatus.Betting[`${item.smid}`][i].bet_now].odds / 1000
        PassStatus.odds /= del
      }
      PassStatus.odds *= (item.odds[item.bet_now].odds / 1000)
      state.PassStatus = PassStatus
    }

    function del () {
      if (state.PassStatus.betLength <= 1) {
        state.PassStatus = PassStatus
      } else {
        PassStatus = copy(state.PassStatus)
        PassStatus.betLength--
        PassStatus.show = true
        cbk(function () {
          PassStatus.odds /= (state.PassStatus.Betting[`${item.smid}`][`${item.smpid}${item.bet_now}`].odds[item.bet_now].odds / 1000)
          delete PassStatus.Betting[`${item.smid}`]
        }, function () {
          if (Object.keys(PassStatus.Betting[`${item.smid}`]).length === 1) {
            delete PassStatus.Betting[`${item.smid}`]
          } else {
            delete PassStatus.Betting[`${item.smid}`][`${item.smpid}${item.bet_now}`]
          }
        })
        state.PassStatus = PassStatus
      }
    }

    function add () {
      if (state.PassStatus.betLength >= 10) {
        Toast('10')
        return
      }
      //    betLength
      PassStatus = copy(state.PassStatus)
      PassStatus.show = true
      PassStatus.betLength++
      cbk(function () {
        PassStatus.Betting[`${item.smid}`] = { [`${item.smpid}${item.bet_now}`]: { ...item } }
        PassStatus.odds *= (item.odds[item.bet_now].odds / 1000)
      }, function () {
        if (PassStatus.Betting[`${item.smid}`]) {
          PassStatus.Betting[`${item.smid}`][`${item.smpid}${item.bet_now}`] = { ...item, Amount: 0 }
        } else {
          PassStatus.Betting[`${item.smid}`] = { [`${item.smpid}${item.bet_now}`]: { ...item, Amount: 0 } }
        }
      })
      state.PassStatus = PassStatus
    }

    function cbk (pass, early) {
      if (patternId * 1 === 2) {
        // 
        pass()
      } else if (patternId * 1 === 1) {
        // 
        early()
      }
    }
  },
  [ADD_EARLY_BET_SUM] (state, { target, sum }) {
    if (target && state.PassStatus.Betting[`${target.smid}`] && state.PassStatus.Betting[`${target.smid}`][`${target.smpid}${target.bet_now}`]) {
      Vue.set(state.PassStatus.Betting[`${target.smid}`][`${target.smpid}${target.bet_now}`], 'Amount', sum)
    }
  },
  [MODIFY_365_DATA] (state, array) {
    // 
    let bet = 0
    array.forEach(item => {
      if (state.PassStatus.Betting[`${item.smid}`][`${item.smpid}${bet}`] || state.PassStatus.Betting[`${item.smid}`][`${item.smpid}${++bet}`]) {
        if (state.PassStatus.Betting[`${item.smid}`][`${item.smpid}${bet}`].status === item.status) {
          Vue.set(state.PassStatus.Betting[`${item.smid}`][`${item.smpid}${bet}`], 'betCustomStatus', true)
        } else {
          const data = copy(state.PassStatus.Betting[`${item.smid}`][`${item.smpid}${bet}`])
          data.status = item.status
          data.betCustomStatus = true
          Vue.set(state.PassStatus.Betting[`${item.smid}`], `${item.smpid}${bet}`, data)
        }
      }
    })
  }
}

export const HomeActions = {
  [GET_365_HOME_DATA] ({ commit, state }, { type, index1, index2, index3 }) {
    /*
    * type=0
    * type=1
    * type=2
    * */
    let inspect1 = false // state.Patternstate.Genre
    let inspect2 = false // state.MatchSchedule
    index1 === undefined && (index1 = state.PatternIndex)
    index2 === undefined && (index2 = state.GenreIndex)
    index3 === undefined && (index3 = state.MatchStatus)
    if (type === 0 || type === 1) {
      inspect1 = true // 
      inspect2 = true // 
    }

    return new Promise((resolve, reject) => {
      const arr = [null, null] // 
      if (inspect1) { // 
        if (!state.Pattern[index1]) arr[0] = http.getGuessPlay()
        if (!state.Genre[index2]) arr[1] = http.getHomeMenu()
      } else {
        arr[0] = http.getGuessPlay()
        arr[1] = http.getHomeMenu()
      }

      Promise.all(arr).then(list => {
        const data = {}
        // 
        if (list[0] && list[0][index1]) {
          data.PatternId = list[0][index1].id
          commit(SET_365_PATTERN_DATA, list[0])
        } else if (list[0] === null && state.Pattern[index1]) {
          data.PatternId = state.Pattern[index1].id
        } else {
          reject(new Error('getGuessPlay'))
        }

        // 
        if (list[1] && list[1][index2]) {
          data.GenreId = list[1][index2].id
          commit(SET_365_GENRE_DATA, list[1])
        } else if (list[1] === null && state.Genre[index2]) {
          data.GenreId = state.Genre[index2].id
        } else {
          reject(new Error('getHomeMenu'))
        }
        resolve(data)
      })
    }).then(({ PatternId, GenreId }) => {
      // 
      return new Promise((resolve, reject) => {
        if (inspect2 && state.MatchSchedule[index1] && state.MatchSchedule[index1][index2] && state.MatchSchedule[index1][index2][index3] && state.MatchSchedule[index1][index2][index3].length) {
          // 
          resolve(false)
        } else {
          Match(GenreId)
        }

        function Match (id) {
          if (index3 === 0) {
            http.getMatchSchedule(id, 0).then(resolve).catch(() => resolve(false))
          } else if (index3 === 1) {
            http.getMatchResult(id).then(resolve).catch(() => resolve(false))
          } else if (index3 === 2) {
            http.getMatchSchedule(id, 1).then(resolve).catch(() => resolve(false))
          } else if (index3 === 3) {
            http.getMatchResult(id).then(resolve).catch(() => resolve(false))
          } else {
            reject(new Error(''))
          }
        }
      })
    }).then((list) => {
      if (list) {
        commit(SET_365_COMPETITION_LIST_DATA, { index1, index2, index3, list })
      }
      if (index1 !== state.PatternIndex) {
        // 
        commit(ADD_DEL_365_THROUGH_DATA, { initial: true, patternId: state.Pattern[state.PatternIndex].id }) // 
        commit(SET_365_PATTERN_INDEX, index1)
      }

      index2 !== state.GenreIndex && commit(SET_365_GENRE_INDEX, index2)

      index3 !== state.MatchStatus && commit(SET_365_FRUIT_RACING_INDEX, index3)
    })
  },
  [SELECTION_RED_ENVELOPES] ({ commit, rootState }, { target, redId }) {
    commit(SELECTION_RED_ENVELOPES_SET, { target, redId, Coupon: rootState.mine.Coupon })
  }
}
